"use client"

import { useState } from "react"
import Image from "next/image"
import { Bed, Bath, Maximize, Share2, MapPin, Play, X } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Card } from "@/components/ui/card"
import { Dialog, DialogContent, DialogTitle, DialogTrigger } from "@/components/ui/dialog"
import { PropertyCarousel } from "@/components/property-carousel" // Usaremos o carrossel que já criamos antes
import type { Property } from "@/types/property"
import { toast } from "sonner"

interface PropertyCardProps {
  property: Property
}

export function PropertyCard({ property }: PropertyCardProps) {
  const [isViewerOpen, setIsViewerOpen] = useState(false)

  const formatPrice = (price: number) => {
    return new Intl.NumberFormat("pt-BR", {
      style: "currency",
      currency: "BRL",
      minimumFractionDigits: 0,
      maximumFractionDigits: 0,
    }).format(price)
  }

  const handleShare = async () => {
    // Link dinâmico com ID
    const url = `${window.location.origin}/imovel/${property.id}`

    try {
      await navigator.clipboard.writeText(url)
      toast.success("Link copiado!")
    } catch {
      // Fallback para celulares antigos
      const textArea = document.createElement("textarea")
      textArea.value = url
      document.body.appendChild(textArea)
      textArea.select()
      document.execCommand("copy")
      document.body.removeChild(textArea)
      toast.success("Link copiado!")
    }
  }

  // Lógica de exibição da área (Hectares vs Metros)
  const formatArea = () => {
    const unit = property.areaUnit === "ha" ? "hectares" : "m²"
    return `${property.area} ${unit}`
  }

  return (
    <>
      <Card className="overflow-hidden border-gray-800 bg-gray-900 hover:border-gray-700 transition-all">
        <div className="relative">
          {/* Botão Invisível que abre o Modal */}
          <div 
            className="relative block aspect-[4/3] w-full overflow-hidden cursor-pointer group"
            onClick={() => setIsViewerOpen(true)}
          >
            <Image
              src={property.coverPhoto || "/placeholder.svg"}
              alt={property.location}
              fill
              className="object-cover transition-transform duration-500 group-hover:scale-105"
            />

            {/* Overlay de Contagem de Fotos */}
            <div className="absolute bottom-3 right-3 flex items-center gap-1 rounded-full bg-black/70 px-2 py-1 text-xs text-white backdrop-blur-sm">
              {property.photos.length} fotos
              {property.videoUrl && (
                <span className="ml-1 flex items-center gap-1 border-l border-white/30 pl-1">
                  <Play className="h-3 w-3" /> vídeo
                </span>
              )}
            </div>

            {/* Overlay de Status (Vendido/Alugado) */}
            {property.status !== "available" && (
              <div className="absolute inset-0 flex items-center justify-center bg-black/60 backdrop-blur-[2px]">
                <span className="text-xl font-bold text-white border-2 border-white px-4 py-1 rounded uppercase tracking-widest">
                  {property.status === "sold" ? "Vendido" : "Alugado"}
                </span>
              </div>
            )}
          </div>

          {/* Badges Flutuantes */}
          <div className="absolute left-3 top-3 flex flex-wrap gap-2">
            <Badge className={property.propertyType === "sale" ? "bg-green-600" : "bg-blue-600"}>
              {property.propertyType === "sale" ? "Venda" : "Aluguel"}
            </Badge>
            {property.financeable && (
              <Badge variant="secondary" className="bg-yellow-500/90 text-white hover:bg-yellow-600">
                Financiável
              </Badge>
            )}
          </div>
        </div>

        {/* Conteúdo do Card */}
        <div className="flex flex-col gap-3 p-4">
          <div className="text-2xl font-bold text-white">
            {formatPrice(property.price)}
            {property.propertyType === 'rent' && <span className="text-sm text-gray-400 font-normal">/mês</span>}
          </div>

          <div className="flex items-start gap-2 text-sm text-gray-400 h-10">
            <MapPin className="mt-0.5 h-4 w-4 shrink-0 text-green-500" />
            <span className="line-clamp-2">{property.location}</span>
          </div>

          {/* Ícones de Características */}
          <div className="flex items-center justify-between text-sm text-gray-300 border-t border-gray-800 pt-3 mt-1">
            <div className="flex items-center gap-1.5" title="Quartos">
              <Bed className="h-4 w-4 text-gray-500" />
              <span>{property.bedrooms}</span>
            </div>
            <div className="flex items-center gap-1.5" title="Banheiros">
              <Bath className="h-4 w-4 text-gray-500" />
              <span>{property.bathrooms}</span>
            </div>
            <div className="flex items-center gap-1.5" title="Área">
              <Maximize className="h-4 w-4 text-gray-500" />
              <span>{formatArea()}</span>
            </div>
          </div>

          {/* Descrição com Rolagem (Scroll) - Seu pedido específico */}
          <div className="h-20 overflow-y-auto rounded-md bg-gray-950/50 p-2 text-xs text-gray-400 leading-relaxed scrollbar-thin scrollbar-thumb-gray-700 scrollbar-track-transparent">
            {property.description || "Sem descrição."}
          </div>

          {/* Ações */}
          <div className="flex items-center justify-between pt-2 gap-2">
            <Button
              className="flex-1 bg-green-600 text-white hover:bg-green-700 font-semibold"
              onClick={() => window.open(`https://wa.me/5538999999999?text=Olá, vi o imóvel ${property.id} no site.`, "_blank")}
            >
              WhatsApp
            </Button>
            <Button
              variant="outline"
              size="icon"
              onClick={handleShare}
              className="border-gray-700 hover:bg-gray-800 text-gray-300"
            >
              <Share2 className="h-4 w-4" />
            </Button>
          </div>
        </div>
      </Card>

      {/* Lightbox (Visualizador) - Reutilizando Dialog */}
      <Dialog open={isViewerOpen} onOpenChange={setIsViewerOpen}>
        <DialogContent className="max-w-[100vw] h-[100vh] bg-black border-none p-0 rounded-none flex items-center justify-center">
            <DialogTitle className="sr-only">Galeria de Fotos</DialogTitle>
            <button 
                onClick={() => setIsViewerOpen(false)}
                className="absolute top-4 right-4 z-50 p-2 bg-black/50 rounded-full text-white hover:bg-white/20"
            >
                <X className="h-6 w-6" />
            </button>
            {/* Aqui entra o carrossel que já otimizamos antes */}
            <div className="w-full h-full flex items-center justify-center">
                <PropertyCarousel photos={property.photos} />
            </div>
        </DialogContent>
      </Dialog>
    </>
  )
}